import css from "./Profile.module.css";
import PostThumbnail from "./PostThumbnail";
import {Link, useParams} from 'react-router-dom';
import React, { useContext } from 'react';
import { StoreContext } from 'contexts/StoreContext';

function Profile(props) {
  let {
    posts:allPosts, users, followers:allFollowers, currentUserId, 
    addFollower, removeFollower
  } = useContext(StoreContext);

  const tryUser = useParams();
  const userId = (tryUser.userId === undefined ? currentUserId : tryUser.userId);
  const user = users.find(u => u.id === userId);
  const posts = allPosts.filter(p => p.userId === userId);
  const followers = allFollowers.filter(f => f.userId === userId);
  const following = allFollowers.filter(f => f.followerId === userId);
  const isFollower = allFollowers.find(f => f.userId === userId && f.followerId === currentUserId) !== undefined;
  
  let button;
  if (isFollower) {
    button = <button onClick={handleUnfollow} className={css.unfollowBtn}>
              Unfollow
            </button>;
  } else {
    button = <button onClick={handleFollow} className={css.followBtn}>
             Follow
            </button>;
  }

  function handleFollow(){
    addFollower(userId, currentUserId);
  }

  function handleUnfollow(){
    removeFollower(userId, currentUserId);
  }

  return (
    <div>
      <header className={css.header}>
        <div className={css.photo}>
          <img src={user.photo} alt="Profile" />
        </div>
        <div className={css.id}>
          <span>{userId}</span>
        </div>
        <div classname={css.button}> 
          {button}
        </div>
      </header>
      <div className={css.user}>
        <div className={css.name}>{user.name}</div>
        <div className={css.bio}>{user.bio}</div>
      </div>
      <ul className={css.activity}>
        <li>
          <span>{posts.length}</span>
          <br />
          posts
        </li>
        <li>
          <span>{followers.length}</span>
          <br />
          followers
        </li>
        <li>
          <span>{following.length}</span>
          <br />
          following
        </li>
      </ul>
      <div className={css.posts}>
        {posts.map(post => (
          <Link key={post.id} to={`/${post.id}`}>
            <PostThumbnail post={post} />
          </Link>
        ))}
      </div>
    </div>
  );
}

export default Profile;
