import React, {createContext, useState, useEffect} from 'react';
import 'firebase/auth';
import uniqueId from "utils/uniqueId.js";
import { useHistory } from "react-router-dom";
import firebase from "firebase/app";
import 'firebase/firestore';

const firebaseConfig = {
  apiKey: "AIzaSyDFRDgHXdGIuiUegOeNiBBDQM81e_PIqGo",
    authDomain: "socialapp-d7767.firebaseapp.com",
    projectId: "socialapp-d7767",
    storageBucket: "socialapp-d7767.appspot.com",
    messagingSenderId: "193443111563",
    appId: "1:193443111563:web:768c0121e797c9f38a2a8e"
};

// Initialize Firebase
firebase.initializeApp(firebaseConfig);
const db = firebase.firestore();
const auth = firebase.auth();

function StoreContextProvider(props){
    /*const [store, setStore] = useState(()=>{
        return JSON.parse(window.localStorage.getItem('store')) || initialStore;
    });

    useEffect(()=>{
        window.localStorage.setItem('store', JSON.stringify(store));
    }, [store]);
    */
    const [currentUserId, setCurrentUserId] = useState(null); // or 'judy'
    const [users, setUsers] = useState([]);
    const [posts, setPosts] = useState([]);
    const [likes, setLikes] = useState([]);
    const [followers, setFollowers] = useState([]);
    const [comments, setComments] = useState([]);
    const history = useHistory();

    useEffect(()=>{// initialization
      db.collection('users').get().then(snapshot=>{
        const users = snapshot.docs.map(d=>d.data());
        setUsers(users);
      });
      db.collection('posts').get().then(snapshot=>{
        const posts = snapshot.docs.map(d=>d.data());
        setPosts(posts);
      });
      db.collection('likes').get().then(snapshot=>{
        const likes = snapshot.docs.map(d=>d.data());
        setLikes(likes);
      });
      db.collection('followers').get().then(snapshot=>{
        const followers = snapshot.docs.map(d=>d.data());
        setFollowers(followers);
      });
      db.collection('comments').get().then(snapshot=>{
        const comments = snapshot.docs.map(d=>d.data());
        setComments(comments);
      })
    }, []);
    
    function login(email, password){
      auth.signInWithEmailAndPassword(email,password).then((response)=>{
        // success
        db.collection('users')
        .where('email','==', response.user.email)
        .get()
        .then(snapshot=>{
          setCurrentUserId(snapshot.docs[0].data().id) //first document's data = user info
          }).catch(error=>{
          // fail
          setCurrentUserId(null);
          })
      });
      history.push('/');
    }

    function addLike(postId) {
      const like = {
        userId: currentUserId,
        postId,
        datetime: new Date().toISOString()
      };
      setLikes(likes.concat(like));
      db.collection('likes').add(like);
    }
  
    function addFollower(userId, followerId){
      // use concat
      const follow = {
        userId: userId,
        followerId: followerId
      }

      setFollowers(followers.concat(follow));
      db.collection('followers').add(follow);
    }

    function removeFollower(userId, followerId){
      // use filter
      setFollowers(followers.filter(follow => !(follow.userId === userId && follow.followerId === followerId)));
      db.collection('followers')
      .where('userId', '==', userId)
      .where('followerId', '==', followerId)
      .get()
      .then(snapshot=>snapshot.forEach(doc=>doc.ref.delete()));
    }
  
    function removeLike(postId) {
      // console.log('removeLike',postId, store.likes.filter(like=>!(like.userId===store.currentUserId && like.postId===postId)));
      setLikes(likes.filter(
        like => !(like.userId === currentUserId && like.postId === postId)
      ));
      db.collection('likes')
      .where('userId', '==', currentUserId)
      .where('postId', '==', postId)
      .get()
      .then(snapshot=>snapshot.forEach(doc=>doc.ref.delete()));
    }
  
    function addComment(postId, text) {
      const comment = {
        userId: currentUserId,
        postId,
        text,
        datetime: new Date().toISOString()
      };
      setComments(comments.concat(comment));
      db.collection('comments').add(comment);
    }

    function addPost(photo, desc) {
      const post = {
        id: uniqueId("post"),
        userId: currentUserId,
        photo,
        desc,
        datetime: new Date().toISOString()
      };
      setPosts(posts.concat(post));
      db.collection('posts').add(post);
    }

	return (
        <StoreContext.Provider value = {{login, posts, users, comments, likes, followers, addComment, addLike, removeLike, addPost, addFollower, removeFollower}}>
            {props.children}
        </StoreContext.Provider>
    )
}
// export the context so that other components can import
export const StoreContext = createContext(); 
export default StoreContextProvider; // export this component as default